<template>
  <div class="register" style="background-color: #fff;">
    <div class="register-bar">
      <van-nav-bar :title="$t('AuditResults1')" left-arrow @click-left="linkHome"/>
    </div>
    <div class="submission">
      <div class="submission-img">
        <img src="../../../assets/examine/adopt.png"/>
      </div>
      <div class="tips">
        <div class="tips-one">{{$t('AuditResults2')}}</div>
        <div>{{$t('AuditResults3')}}</div>
        <div>{{$t('AuditResults4')}}</div>
      </div>
      <div class="again again1" @click="setFundPassword">{{$t('AuditResults5')}}</div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'adopt',
  methods: {
    // 返回上级
    linkHome () {
      this.$router.push({
        name:'home'
      })
    },
    // 设置资金密码
    setFundPassword(){
      this.$router.push({
        name:'setFundPassword',
        params:{
          modify:'my'
        }
      })
    }

  }
}
</script>

<style scoped lang="scss">
  @import "../../../style/register";
</style>



